<template>
    <div class="sidebar-wrapper"
        ref="sidebarWrapper"
        v-show="enableSwipeOut"
        :class="wrapperClass"
    >
        <div class="sidebar-scroller"
            ref="sidebarScroller"
            :style="{
                'padding-left': widthProp
            }"
        >
            <div class="sidebar-main"
                :style="{
                    width: widthProp
                }"
                @scroll.stop
            >
                <slot></slot>
            </div>
            <div class="touch-toggle"
                :style="{
                    'opacity': opacity,
                    'padding-left': widthProp
                }"
                ref="sidebarToggle"
                @touchstart="toggleTouchStart"
                @touchmove="toggleTouchMove"
                @click.stop.prevent="toggleClick"
            ></div>
        </div>
    </div>
</template>

<script>
import IScroll from 'iscroll/build/iscroll-lite';

const rAF = window.requestAnimationFrame
    || window.webkitRequestAnimationFrame
    || function (cb) {
        window.setTimeout(cb, 1000 / 60);
    };

export default {
    props: {
        value: {
            'type': Boolean,
            'default': false
        },
        width: {
            'type': Number,
            'default': 225
        },
        enableSwipeOut: {
            'type': Boolean,
            'default': false
        }
    },
    data() {
        return {
            showStatus: this.value,
            startX: 0,
            startY: 0,
            wrapperClass: {
                'expand': false,
                'collapse': true,
                'w-left': true
            },
            opacity: 0,
            iscroll: null
        };
    },
    computed: {
        itsWidth() {
            return this.width < 1
                ? Math.round(this.width * document.documentElement.clientWidth)
                : this.width;
        },
        widthProp() {
            return this.itsWidth + 'px';
        }
    },
    watch: {
        value() {
            this.showStatus = this.value;
        },
        showStatus(val) {
            this.toggleScroll(val);
            this.$emit('input', val);
        }
    },
    methods: {

        /**
         * sidebar toggle touchstart 
         *  touch 
         *
         * @param {Event} e 
         */
        toggleTouchStart(e) {
            if (!this.enableSwipeOut) {
                return;
            }

            if (this.wrapperClass.expand) {
                return;
            }

            let {clientX, clientY} = e.touches[0];
            this.startX = clientX;
            this.startY = clientY;
        },

        /**
         * sidebar toggle touchmove 
         *  sidebar 
         *
         * @param {Event} e 
         */
        toggleTouchMove(e) {

            if (!this.enableSwipeOut) {
                return;
            }

            if (this.wrapperClass.expand) {
                return;
            }

            let {clientX, clientY} = e.touches[0];
            let x = clientX - this.startX;

            //  5 
            //  30  sidebar 
            if (x > 5 && Math.abs(clientY - this.startY) / x < 0.577) {
                this.wrapperClass.expand = true;
                this.wrapperClass.collapse = false;

                this.$nextTick(() => {
                    this.bindScroll(e);
                });
            }
        },

        /**
         *  sidebar toggle 
         *
         * @param {Event} e 
         */
        toggleClick(e) {
            if (this.iscroll) {
                this.forceToggleScroll(false);
            }
        },

        /**
         *  iscroll
         *
         * @param {Event} e  touchmove 
         */
        bindScroll(e) {
            if (this.iscroll) {
                return;
            }
            //  iscroll
            this.iscroll = new IScroll(this.$refs.sidebarWrapper, {
                eventPassthrough: true,
                scrollY: false,
                scrollX: true,
                bounce: false,
                startX: -this.itsWidth
            });
            // 
            this.changeOpacity();

            this.iscroll.on('scrollEnd', () => {
                let {directionX, x} = this.iscroll;
                //  showStatus  true
                if (x === 0) {
                    this.showStatus = true;
                    return;
                }
                //  showStatus  false  iscroll
                if (x === -this.itsWidth) {
                    this.unbindScroll();
                    this.showStatus = false;
                    return;
                }
                //  
                if (directionX > 0) {
                    this.forceToggleScroll(false);
                }
                else if (directionX < 0) {
                    this.forceToggleScroll(true);
                }
                else {
                    this.showStatus = !this.showStatus;
                }
            });
            //  iscroll 
            e && this.iscroll._start(e);
        },

        /**
         *  iscroll
         */
        unbindScroll() {
            if (!this.iscroll) {
                return;
            }
            //  iscroll
            this.iscroll.destroy();
            this.iscroll = null;
            // 
            this.wrapperClass.expand = false;
            this.wrapperClass.collapse = true;
            this.opacity = 0;
            //  iscroll  style
            this.$refs.sidebarScroller.setAttribute(
                'style',
                `padding-left:${this.widthProp}`
            );
        },

        /**
         *  iscroll 
         *
         * @param {boolean} val true ->  false -> 
         */
        toggleScroll(val) {
            if (val) {
                this.wrapperClass.expand = true;
                this.wrapperClass.collapse = false;
                //  wrapper  class 
                //  nextTick
                this.$nextTick(() => {
                    if (!this.iscroll) {
                        this.bindScroll();
                    }

                    if (this.iscroll.x < 0) {
                        //  iscroll  scrollTo 
                        //  nextTick 
                        setTimeout(() => {
                            this.iscroll && this.iscroll.scrollTo(0, 0, 200);
                        }, 10);
                    }
                });
            }
            else {
                if (this.iscroll && this.iscroll.x > -this.itsWidth) {
                    //  scrollTo   scrollEnd  bug
                    setTimeout(() => {
                        this.iscroll.scrollTo(-this.itsWidth, 0, 200);
                    });
                }
            }
        },

        /**
         *  iscroll 
         *
         * @param {boolean} val 
         */
        forceToggleScroll(val) {
            if (this.showStatus === val) {
                this.toggleScroll(val);
            }
            else {
                this.showStatus = val;
            }
        },

        /**
         *  mask 
         */
        changeOpacity() {
            if (this.wrapperClass.expand && this.iscroll) {
                // this.opacity = (this.iscroll.x + this.itsWidth) / this.itsWidth * 0.5;
                setTimeout(() => {
                    if (!this.iscroll) {
                        return;
                    }
                    this.opacity = (this.iscroll.x + this.itsWidth) / this.itsWidth * 0.5;
                }, 80);
                rAF(this.changeOpacity.bind(this));
            }
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.sidebar-wrapper
    z-index 9999

    .sidebar-main
        overflow-y auto
        overflow-x hidden
        box-sizing border-box
        z-index 25
        display none
        background #fff
        height 0
        top 0

    .touch-toggle
        position fixed
        top 0
        bottom 0
        left 0
        width 40px
        z-index 100
        opacity 0
        transition opacity .3s

    &.collapse
        margin-top 0 !important
        .sidebar-scroller
            padding-left 0 !important
        .touch-toggle
            padding-left 0 !important
            top 92px

    &.expand
        position fixed
        top 0
        right 0
        bottom 0
        left 0
        z-index 150
        overflow hidden
        box-shadow 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12)

        .sidebar-scroller
            width 100%
            height 100%
            white-space nowrap
            position relative
            box-sizing content-box

        .sidebar-main
            display block
            height 100%
            position absolute
            top 0
            right auto
            bottom 0
            left 0
            overflow auto

        .touch-toggle
            position static
            width 100%
            height 100%
            background #212121
            opacity .5

</style>



